import {
  Component,
  OnInit,
  AfterViewInit,
  HostListener,
  ChangeDetectorRef
} from '@angular/core';
import { GRAPH_OPTIONS } from '@shared/constants/graph-view';
import { PortfolioService } from '@modules/dashboard/api/portfolio/portfolio.service';

@Component({
  selector: 'app-portfolio',
  templateUrl: './portfolio.component.html',
  styleUrls: ['./portfolio.component.scss']
})
export class PortfolioComponent implements OnInit, AfterViewInit {
  porfolioFilters = Object.values(GRAPH_OPTIONS);
  selectedFilter = this.porfolioFilters[0];
  portfolioValues = [
    { month: 1, value: 3400 },
    { month: 2, value: 1400 },
    { month: 3, value: 640 },
    { month: 4, value: 3460 },
    { month: 5, value: 1400 },
    { month: 6, value: 840 },
    { month: 7, value: 900 },
    { month: 8, value: 340 },
    { month: 9, value: 408 },
    { month: 10, value: 222 },
    { month: 11, value: 1000 },
    { month: 12, value: 8000 }
  ];

  sampleImage = `/Docs/PropertyAsset/Properties/1/PropertyPhotos/1.jpg`;
  hasFunds = true;
  tabletViewMaxSize = 768;
  tabletViewMinSize = 426;
  desktopView;
  tabletView;
  mobileView;

  @HostListener('window:resize', ['$event'])
  onResize() {
    setTimeout(() => {
      this.checkViewPorts();
      this.initIcons();
    }, 500);
  }

  constructor(
    private changeDetectorRef: ChangeDetectorRef,
    private _portfolioService: PortfolioService
  ) {}

  ngOnInit() {}

  ngAfterViewInit() {
    this.initIcons();
    this.checkViewPorts();
    // this.getPortfolioValue();
  }

  setSelectedFilter(evt) {
    this.selectedFilter = evt;
  }

  initIcons() {
    const iconIncomeSvg = document.querySelector('#icon-income-blue')
      .firstElementChild;
    let iconIncome = iconIncomeSvg.innerHTML || '';
    iconIncome = iconIncome.replace(`fill="#788195"`, `fill="#6580B6"`);
    iconIncome = iconIncome.replace(`width="28px"`, `width="25px"`);
    iconIncome = iconIncome.replace(`height="36px"`, `height="33px"`);
    iconIncomeSvg.innerHTML = iconIncome;

    const iconCapitalSvg = document.querySelector('#icon-capital-blue')
      .firstElementChild;
    let iconCapital = iconCapitalSvg.innerHTML || '';
    iconCapital = iconCapital.replace(`fill="#788195"`, `fill="#6580B6"`);
    iconCapital = iconCapital.replace(`width="32px"`, `width="28px"`);
    iconCapital = iconCapital.replace(`height="32px"`, `height="28px"`);
    iconCapitalSvg.innerHTML = iconCapital;

    const iconFilterSvg = document.querySelector('#icon-filter')
      .firstElementChild;
    let iconFilter = iconFilterSvg.innerHTML || '';
    iconFilter = iconFilter.replace(`fill="#626262"`, `fill="#626262"`);
    iconFilter = iconFilter.replace(`width="35"`, `width="22px"`);
    iconFilter = iconFilter.replace(`height="35"`, `height="22px"`);
    iconFilterSvg.innerHTML = iconFilter;
  }

  checkViewPorts() {
    this.desktopView = window.outerWidth >= this.tabletViewMaxSize;
    this.tabletView =
      window.outerWidth <= this.tabletViewMaxSize &&
      window.outerWidth > this.tabletViewMinSize;
    this.mobileView = window.outerWidth <= this.tabletViewMinSize;
    if (this.changeDetectorRef['destroyed']) {
      this.changeDetectorRef.detectChanges();
    }
    if (this.mobileView) {
      this.initMobileIcons();
    }
  }

  initMobileIcons() {
    if (!this.hasFunds) {
      const iconAssetSvg = document.querySelector('#icon-asset-gray')
        .firstElementChild;
      let iconAsset = iconAssetSvg.innerHTML || '';
      iconAsset = iconAsset.replace(`fill="#2C2C2C"`, `fill="#D1D1D1"`);
      iconAssetSvg.innerHTML = iconAsset;

      const iconIncomeV2Svg = document.querySelector('#icon-income-v2-gray')
        .firstElementChild;
      let iconIncomeV2 = iconIncomeV2Svg.innerHTML || '';
      iconIncomeV2 = iconIncomeV2.replace(`fill="#2C2C2C"`, `fill="#D1D1D1"`);
      iconIncomeV2Svg.innerHTML = iconIncomeV2;

      const iconGrowthV2Svg = document.querySelector('#icon-growth-v2-gray')
        .firstElementChild;
      let iconGrowthV2 = iconGrowthV2Svg.innerHTML || '';
      iconGrowthV2 = iconGrowthV2.replace(`fill="#2C2C2C"`, `fill="#D1D1D1"`);
      iconGrowthV2Svg.innerHTML = iconGrowthV2;

      const iconReturnsSvg = document.querySelector('#icon-returns-gray')
        .firstElementChild;
      let iconReturns = iconReturnsSvg.innerHTML || '';
      iconReturns = iconReturns.replace(`fill="#2C2C2C"`, `fill="#D1D1D1"`);
      iconReturns = iconReturns.replace(`fill="#2C2C2C"`, `fill="#D1D1D1"`);
      iconReturns = iconReturns.replace(`fill="#000000"`, `fill="#D1D1D1"`);
      iconReturnsSvg.innerHTML = iconReturns;
    }
  }

  getPortfolioValue() {
    this._portfolioService
      .getPortfolioValue(`minutemaid@gmail.com`, { TimeFrame: `Last3Months` })
      .subscribe(data => {
        // console.log(data);
      });
  }
}
