import { Injectable } from '@angular/core';
import { environment } from '@env/environment';

import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

// import { Property, Country } from './property.model';
import { preorderDashboardBuilderUI } from './preorder-dashboard.mapper';

@Injectable({
  providedIn: 'root'
})
export class PreorderDashboardService {
  baseUrl = `${environment.baseUrl}/api`;

  constructor(private _http: HttpClient) {}

  getTempDetails(userName): Observable<any> {
    return this._http
      .get(`${this.baseUrl}/users/${userName}/temp-dashboard`)
      .pipe(map(preorderDashboardBuilderUI));
  }
}
