import {
  Component,
  OnInit,
  ChangeDetectorRef,
  AfterViewInit,
  HostListener
} from '@angular/core';
import { PreorderDashboardService } from '@modules/dashboard/api/preorder-dashboard/preorder-dashboard.service';
import { SessionStorageService } from '@shared/services/session-storage/session-storage.service';

@Component({
  selector: 'preorder-dashboard',
  templateUrl: './preorder-dashboard.component.html',
  styleUrls: ['./preorder-dashboard.component.scss']
})
export class PreorderDashboardComponent implements OnInit, AfterViewInit {
  constructor(
    private changeDetectorRef: ChangeDetectorRef,
    private _preorderDashboardService: PreorderDashboardService,
    private _sessionStorageService: SessionStorageService
  ) {}

  tabletViewMaxSize = 768;
  tabletViewMinSize = 426;
  desktopView;
  tabletView;
  mobileView;
  preOrderDetails;
  slideHeight = `400px`;
  slideWidth = `620px`;

  @HostListener('window:resize', ['$event'])
  onResize() {
    setTimeout(() => {
      this.checkViewPorts();
    }, 500);
  }

  ngOnInit() {
    this.getTempDetails();
  }

  ngAfterViewInit() {
    this.checkViewPorts();
  }

  checkViewPorts() {
    this.desktopView = window.outerWidth >= this.tabletViewMaxSize;
    this.tabletView =
      window.outerWidth <= this.tabletViewMaxSize &&
      window.outerWidth > this.tabletViewMinSize;
    this.mobileView = window.outerWidth <= this.tabletViewMinSize;

    this.updateCarouselSize();
  }

  updateCarouselSize() {
    console.log(this.slideHeight);
    console.log(this.desktopView);
    if (this.preOrderDetails) {
      if (this.desktopView) {
        this.slideHeight = `400px`;
        this.slideWidth = `620px`;
      } else if (this.tabletView || this.mobileView) {
        const leftWidth = document.querySelector('#pre-order-wrapper-id')
          .clientWidth;
        this.slideHeight = `${leftWidth - 150}px`;
        this.slideWidth = `${leftWidth}px`;
      }
      this.changeDetectorRef.detectChanges();
    }
  }

  getTempDetails() {
    const userName = JSON.parse(localStorage.getItem('currentUser')).username;
    this._preorderDashboardService.getTempDetails(userName).subscribe(data => {
      this.preOrderDetails = data;
      console.log(data);
    });
  }
}
