import { Injectable } from '@angular/core';
import { environment } from '@env/environment';

import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

// import { Property, Country } from './property.model';
import { portfolioValueBuilderUI } from './portfolio.mapper';

@Injectable({
  providedIn: 'root'
})
export class PortfolioService {
  baseUrl = `${environment.baseUrl}/api`;

  constructor(private _http: HttpClient) {}

  getPortfolioValue(userName, params): Observable<any> {
    return this._http
      .post(`${this.baseUrl}/${userName}/portfolio-value`, params)
      .pipe(map(portfolioValueBuilderUI));
  }
}
